<script>
import axios from "axios";
const HOST = "http://3.133.45.252:3000";

export default {
  data() {
    return {
      err_message: "",
      radioCheck: false,
    };
  },
  methods: {
    checkInput() {
      if (!this.radioCheck) {
        this.err_message = "    .";
        return false;
      }
    },
    leaveProcess() {
      if (this.checkInput() === false) {
        alert(this.err_message);
      } else {
        try {
          axios.post(`${HOST}/auth/help/leave`).then((res) => {
            if (res.status === 200) {
              console.log(res);
              alert(
                " .   ."
              );
            }
          });
        } catch (error) {
          console.log(error);
        }
      }
    },
  },
};
</script>

