<template>
  <div>
    <div id="Container">
      <h1 id="titleContainer"> </h1>
      <div id="formSection">
        <form method="post">
          <input
            size="35"
            type="password"
            name="newPassword"
            placeholder=" "
            v-model="newPassword"
          />
          <input
            size="35"
            type="password"
            name="newPasswordCheck"
            placeholder=" "
            v-model="newPasswordCheck"
          />
        </form>
      </div>
      <div id="verifySection">
        <div id="section1">
          <form>
            <input
              size="24"
              type="text"
              placeholder="(-)  "
              v-model="phoneNumber"
            />
          </form>
          <button class="smsBtn" v-on:click="sendSMS">
            <br />
          </button>
        </div>
        <div id="section2" v-if="showVerify">
          <input
            size="24"
            type="text"
            placeholder=" "
            v-model="verifyCode"
          />
          <button class="smsBtn" v-on:click="verifyCodeCheck">
            <br />
          </button>
        </div>
      </div>

      <div id="btn">
        <button class="menuBtn" v-on:click="changePw"></button>
        <button class="menuBtn" v-on:click="cancel"></button>
      </div>
    </div>
  </div>
</template>

<script>
// import Modal from "./common/Modal.vue";
import axios from "axios";
const HOST = "http://3.133.45.252:3000";

export default {
  components: {
    // Modal,
  },
  data() {
    return {
      newPassword: "",
      newPasswordCheck: "",
      err_message: "  .",
      phoneNumber: "",
      verifyCode: "",
      showVerify: false,
      checkVerify: false,
      showModal1: false,
      showModal2: false,
    };
  },
  methods: {
    checkInput() {
      if (!this.newPassword) {
        this.err_message = "  .";
        return false;
      } else if (!this.newPasswordCheck) {
        this.err_message = "  .";
        return false;
      }
    },
    checkPassword() {
      if (this.newPassword != this.newPasswordCheck) {
        this.err_message = "    .";
        return false;
      }
    },
    sendSMS() {
      if (!this.phoneNumber) {
        alert(" .");
      } else {
        try {
          const formData = new FormData();
          formData.append("contact", this.phoneNumber);
          axios.post(`${HOST}/auth/sendSMS`, formData).then((res) => {
            if (res.status === 200) {
              console.log(res);
              alert(" ..");
              this.showVerify = true;
            }
          });
        } catch (error) {
          console.log(error);
        }
      }
    },
    verifyCodeCheck() {
      try {
        const formData = new FormData();
        formData.append("contact", this.phoneNumber);
        formData.append("verifyCode", this.verifyCode);
        axios.post(`${HOST}/auth/verifySMS`, formData).then((res) => {
          if (res.status === 200) {
            console.log(res);
            alert(" !");
            this.checkVerify = true;
          }
        });
      } catch (error) {
        console.log(error);
      }
    },
    changePw() {
      if (this.checkInput() === false) {
        alert(this.err_message);
      } else if (this.checkPassword() === false) {
        alert(this.err_message);
      } else if (this.checkVerify === false) {
        alert("  .");
      } else {
        const formData = new FormData();
        formData.append("newPassword", this.newPassword);

        try {
          axios.post(`${HOST}/help/resetPW`, formData).then((res) => {
            if (res.status === 200) {
              console.log("  !");
              console.log(res);
              try {
                axios.get(`${HOST}/auth/logout`, {}).then((res) => {
                  if (res.status === 200) {
                    this.$router.push("/");
                    alert(" .  .");
                  }
                });
              } catch (error) {
                console.log(error);
              }
            }
          });
        } catch (error) {
          console.log(error);
        }
      }
    },
    cancel() {
      this.$router.push("/userProfile");
    },
  },
};
</script>

